	            size: 180,
	            value: obj.avgScore4/(obj.scoreNum4 * 5) * 100,
	            counterclockwise: true,
	            startAngle: -75,
				color: "#056C9E",
				backgroundColor: "#EEEEEE",
	            text: false,
	            onDraw: function(el, circle) {
	                circle.text(Math.round(circle.value/100*25) + "/25");
	            }
	        });
        },
        countPage() {
        	let html1 = '<div class="page_shadow"><div class="page pdf-page">';
        	let html2 = '</div></div>';
        	let allHtml = '<div class="page_shadow"><div class="page pdf-page"><p class="big_page_number">2</p>';
        	let pageNumber = 2;
        	let height = 1170;
        	let page_content_height = 0;
        	$('.page_content').each(function() {
        		let nowHtml = $(this).html();
        		let nowHeight = $(this).height();
        		page_content_height += nowHeight;
        		if(page_content_height <= height) {
        			allHtml = allHtml + nowHtml;
        		}else {
        			pageNumber ++;
        			let elPageNumber = '<p class="big_page_number">' + pageNumber + '</p>';
        			page_content_height = nowHeight
        			allHtml = allHtml + html2 + html1 +  nowHtml + elPageNumber;
        		}
        	})
        	allHtml  = allHtml + html2;
        	$('.pdf').append(allHtml)
        }
    },
    watch: {
